<template>
  <div class="d-flex flex-column align-center pa-4">
    <div style="width: 100%">
      <h1>This is a hero page</h1>
      <form @submit.prevent="updateProfile">
        <div v-if="getSetHeroName" class="d-flex flex-column">
          <label for="name" class="mb-2">Name</label>
          <v-text-field
            autocomplete="off"
            outlined
            type="text"
            id="name"
            placeholder="name surname"
            v-model="getSetHeroName"
          />
        </div>
        <v-btn color="primary" type="submit">Save</v-btn>
      </form>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations, mapActions } =
  createNamespacedHelpers("profile");
export default {
  name: "HeroView",
  computed: {
    ...mapState(["profile"]),
    getSetHeroName: {
      get() {
        return this.profile && this.profile.hero.name;
      },
      set(newVal) {
        this.SET_HERO_NAME(newVal);
      },
    },
  },
  methods: {
    ...mapMutations(["SET_HERO_NAME"]),
    ...mapActions(["fetchProfile", "updateProfile"]),
  },
  created() {
    this.fetchProfile();
  },
};
</script>

